
// Button
// ––––––––––––––––––––––––––––––––––––––––––––––––––

// adding the :not qualifier is a necessary hack because these styles
// interfere with the datepicker arrows
button:not([type='button']),
.button,
input[type='button'],
input[type='reset'],
input[type='submit'],
input[type='file'], {
	background-color: $color-neutral-secondary;
	border: calc(.1rem * .625) solid $color-neutral-secondary;
	border-radius: calc(.4rem * .625);
	color: $color-initial;
	cursor: pointer;
	display: inline-block;
	font-size: .8rem;
	font-weight: 700;
	height: calc(3.2rem * .625);
	letter-spacing: calc(.1rem * .625);
	line-height: calc(3.2rem * .625);
	padding: 0 calc(3.0rem * .625);
	text-align: center;
	text-decoration: none;
	text-transform: uppercase;
	white-space: nowrap;

	&:focus,
	&:hover {
		&,
		& + label.button {
			background-color: $color-primary;
			border-color: $color-primary;
			color: $color-initial;
			outline: 0; } }

	&:focus {
		&,
		& + label.button {
			outline-offset: -4px;
			outline-style: {}
			outline: 1px solid white; } }

	&[disabled] {
		cursor: default;
		opacity: .5;

		&:focus,
		&:hover {
			background-color: $color-neutral-secondary;
			border-color: $color-neutral-secondary; } }

	&.button-outline {
		background-color: transparent;
		color: $color-neutral-secondary;

		&:focus,
		&:hover {
			background-color: transparent;
			border-color: $color-primary;
			color: $color-primary; }

		&[disabled] {

			&:focus,
			&:hover {
				border-color: inherit;
				color: $color-neutral-secondary; } } }

	&.button-clear {
		background-color: transparent;
		border-color: transparent;
		color: $color-neutral-secondary;

		&:focus,
		&:hover {
			background-color: transparent;
			border-color: transparent;
			color: $color-primary; }

		&[disabled] {

			&:focus,
			&:hover {
				color: $color-neutral-secondary; } } } }
