
// Spacing
// ––––––––––––––––––––––––––––––––––––––––––––––––––

.button,
button,
dd,
dt,
li {
	margin-bottom: calc(1.0rem * .625); }

fieldset,
input,
select,
textarea {
	margin-bottom: calc(1.5rem * .625); }

blockquote,
dl,
figure,
form,
ol,
p,
pre,
table,
ul {
	margin-bottom: calc(2.5rem * .625); }
