
// Typography
// ––––––––––––––––––––––––––––––––––––––––––––––––––

b,
strong {
	font-weight: bold; }

p {
	margin-top: 0; }

h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: 300;
	letter-spacing: -calc(.1rem * .625);
	margin-bottom: calc(2.0rem * .625);
	margin-top: 0; }

h1 {
	font-size: calc(4.6rem * .625);
	line-height: 1.2; }

h2 {
	font-size: calc(3.6rem * .625);
	line-height: 1.25; }

h3 {
	font-size: calc(2.8rem * .625);
	line-height: 1.3; }

h4 {
	font-size: calc(2.2rem * .625);
	letter-spacing: -calc(.08rem * .625);
	line-height: 1.35; }

h5 {
	font-size: calc(1.8rem * .625);
	letter-spacing: -calc(.05rem * .625);
	line-height: 1.5; }

h6 {
	font-size: calc(1.6rem * .625);
	letter-spacing: 0;
	line-height: 1.4; }
